export default {
	data() {
		return {
			isCard: true,
			sayList: null,
			index: '',
			isthumbColor: "#ff5500",
			discussList: [],
			discuss: ""
		}
	},
	onLoad(e) {
		this.sayList = uni.getStorageSync('recommendList')
		console.log(this.sayList)
		this.index = e.index
		console.log(this.sayList[e.index].picUrl)
		
		uni.request({
		    url: this.Server_IP + 'queryDiscuss', //仅为示例，并非真实接口地址。
		    data: {
				textId: this.sayList[this.index].textId
		    },
		    header: {
		        'custom-header': 'queryDiscuss' //自定义请求头信息
		    },
			method:"POST",
			dataType:"json",
		    success: (res) => {
		        console.log(res.data);
				if(res.data.info.code == '0'){
					this.discussList = JSON.parse(res.data.data.discuss)
				}else{
					uni.showToast({
						icon: 'none',
						title: "获取评论失败"
					});
				}
		    },
		});
	},
	methods: {
		ipreviewImage(idx, picUrl) {
			console.log("11")
			console.log(idx)
			console.log(picUrl)
			let preview = picUrl;
			uni.previewImage({
				current: idx,
				urls: preview
			});
		},
		getTimeFormat(timeStamp){
			var time = ""
			var date = new Date(parseInt(timeStamp))
			time += date.getFullYear() + "年"
			time += date.getMonth()+1 + "月"
			time += date.getDate() + "日 "
			time += date.getHours()+ ":"
			time += date.getMinutes() + ":"
			time += date.getSeconds()
			return time
		},
		Thumb(){
			if(this.sayList[this.index].isThumb == "0"){
				this.addThumb()
			}else{
				this.delThumb()
			}
		},
		addThumb(){
			console.log("点赞")
			
			uni.request({
			    url: this.Server_IP + 'addThumb', //仅为示例，并非真实接口地址。
			    data: {
					userId: uni.getStorageSync('userId'),
					textId: this.sayList[this.index].textId
			    },
			    header: {
			        'custom-header': 'addThumb' //自定义请求头信息
			    },
				method:"POST",
				dataType:"json",
			    success: (res) => {
			        console.log(res.data);
					if(res.data.info.code == '0'){
						uni.showToast({
							icon: 'none',
							title: "点赞成功"
						});
						this.sayList[this.index].thumb = +this.sayList[this.index].thumb + 1
						this.sayList[this.index].isThumb = "1"
						
						uni.setStorageSync("recommendList", this.sayList)
					}else{
						uni.showToast({
							icon: 'none',
							title: "点赞失败"
						});
					}
			    },
			});
		},
		delThumb(index){
			console.log("取消点赞")
			
			uni.request({
			    url: this.Server_IP + 'delThumb', //仅为示例，并非真实接口地址。
			    data: {
					userId: uni.getStorageSync('userId'),
					textId: this.sayList[this.index].textId
			    },
			    header: {
			        'custom-header': 'delThumb' //自定义请求头信息
			    },
				method:"POST",
				dataType:"json",
			    success: (res) => {
			        console.log(res.data);
					if(res.data.info.code == '0'){
						uni.showToast({
							icon: 'none',
							title: "取消成功"
						});
						this.sayList[this.index].thumb = +this.sayList[this.index].thumb - 1
						this.sayList[this.index].isThumb = "0"
						uni.setStorageSync("recommendList", this.sayList)
					}else{
						uni.showToast({
							icon: 'none',
							title: "取消失败"
						});
					}
			    },
			});
		},
		todiscuss(){
			console.log(this.discuss)
			uni.request({
			    url: this.Server_IP + 'addDiscuss', //仅为示例，并非真实接口地址。
			    data: {
					userId: this.sayList[this.index].userId,
					textId: this.sayList[this.index].textId,
					sayUserId: uni.getStorageSync('userId'),
					discuss: this.discuss,
			    },
			    header: {
			        'custom-header': 'addDiscuss' //自定义请求头信息
			    },
				method:"POST",
				dataType:"json",
			    success: (res) => {
			        console.log(res.data);
					if(res.data.info.code == '0'){
						uni.showToast({
							icon: 'none',
							title: "评论成功"
						});
						this.sayList[this.index].discuss = +this.sayList[this.index].discuss + 1
						var discusstemp = {
							userId: this.sayList[this.index].userId,
							textId: this.sayList[this.index].textId,
							sayUserId: uni.getStorageSync('userId'),
							discuss: this.discuss,
							moment: (new Date()).getTime()
						}
						this.discussList.splice(0,0,discusstemp)
						this.discuss = ""
						uni.setStorageSync("recommendList", this.sayList)
					}else{
						uni.showToast({
							icon: 'none',
							title: "评论失败"
						});
					}
			    },
			});
		}
	}
}
